import { makeId } from '@/utils/ids';

export class Connection {
    constructor(me, name, call, stream) {
        if (!me || !name) throw new Error('Invalid connection')

        this.me = me
        this.name = name;
        this.id = makeId(this.me.room, this.name);

        this.call = call || null;
        this.stream = stream || null;
        this.video = null;
    }

    get active() { return !!this.call && !!this.stream }

    setVideo(video) {
        this.video = video
        if (this.video) {
            if (this.stream) {
                this.video.srcObject = this.stream
                this.video.oncanplay = () => this.video.play()
            } else {
                this.video.pause()
                this.video.srcObject = null
            }
        }
    }

    async connect() {
        this.disconnect();
        let stream = await this.me.getStream();
        this.call = this.me.peer.call(this.id, stream, { metadata: { name: this.me.name } } );
        this.call.on('stream', (stream) => {
            this.stream = stream;
        });
        this.call.on('close', () => {
            this.disconnect()
        })        
    }

    disconnect() {
        if (this.call) {
            this.call.close();
            this.call = null;
        }
        this.stream = null;
        this.me.checkAllDisconnected()
    }
}

export default Connection;