import { makeId } from '@/utils/ids'
import Connection from '@/models/connection'
import Peer from 'peerjs'
import Vue from 'vue'
import { getDesktop, getVideo } from '../utils/media'
import { some, forEach } from 'lodash'

const config = {
    // key: 'vcs-key',
    // host: window.location.hostname,
    // port: 9000,
    // path: '/vcs-path',
    config: {
        debug: 2,
        iceServers: [
            { url: 'stun:stun.l.google.com:19302' },
            { url: 'stun:stun.l.google.com:19302' },
            { url: 'stun:stun1.l.google.com:19302' },
            { url: 'stun:stun2.l.google.com:19302' },
            { url: 'stun:stun3.l.google.com:19302' },
            { url: 'stun:stun4.l.google.com:19302' },
            { url: 'stun:stun01.sipphone.com' },
            { url: 'stun:stun.ekiga.net' },
            { url: 'stun:stun.fwdnet.net' },
            { url: 'stun:stun.ideasip.com' },
            { url: 'stun:stun.iptel.org' },
            { url: 'stun:stun.rixtelecom.se' },
            { url: 'stun:stun.schlund.de' },
            { url: 'stun:stunserver.org' },
            { url: 'stun:stun.softjoys.com' },
            { url: 'stun:stun.voiparound.com' },
            { url: 'stun:stun.voipbuster.com' },
            { url: 'stun:stun.voipstunt.com' },
            { url: 'stun:stun.voxgratia.org' },
            { url: 'stun:stun.xten.com' }
        ]    
    }
}

export class Me {
    constructor(room, name) {
        this.listening = false;
        this.room = room;
        this.name = name;
        this.id = null;

        this.config = config;
        
        this.showDesktop = false;

        this.stream = null;
        this.peer = null;
        this.connections = {};
    }

    async getStream() {
        if (!this.stream)
            this.stream = await (this.showDesktop ? getDesktop() : getVideo())
        return this.stream;
    }

    setName(room, name) {
        this.disconnect();

        room = room || ''
        if (name && name.length > 3) {
            this.room = room
            this.name = name
            this.id = makeId(room, name)

            this.connect();
        }
    }

    disconnect() {
        for(var c in this.connections) {
            this.connections[c].disconnect()
            Vue.delete(this.connections, c)
        }

        if (this.peer) {
            this.peer.destroy();
            this.peer = null;
        }
    }

    checkAllDisconnected() {
        if (this.stream) {
            if (!some(this.connections, (c) => c.active)) {
                forEach(this.stream.getTracks(), (track) => track.stop())
                this.stream = null
            }
        }
    }

    connect() {
        this.disconnect();

        if (this.name && this.name.length > 3)
        {
            this.peer = new Peer(this.id, this.config);
            this.peer.on('open', async (id) => {
                this.listening = true;
                this.id = id;
            })
            
            this.peer.on('disconnected', () => { this.listening = false; })
            this.peer.on('error', (err) => { this.listening = false; console.warn(err) })
            this.peer.on('call', async (call) => {
                call.on('stream', (stream) => {
                    let newConn = new Connection(this, call.metadata.name, call, stream)
                    Vue.set(this.connections, call.peer, newConn)
                })
                call.on('close', () => {
                    let conn = this.connections[call.peer]
                    if (conn)
                        conn.disconnect()
                })
                let stream = await this.getStream();
                call.answer(stream);
            })
        }
    }

    connectTo(otherName) {
        let otherId = makeId(this.room, otherName)
        if (this.peer) {
            let newConn = new Connection(this, otherName);
            Vue.set(this.connections, otherId, newConn)
        }
    }
}

export default Me;