<template>
    <v-toolbar-items>
      <v-btn icon @click="connect()"><v-icon>{{connectIcon}}</v-icon></v-btn>

      <portal to="toolbar">
        <div class="d-flex d-flex-row">
          <v-text-field 
            solo-inverted flat hide-details dense rounded single-line
            v-model="room"
            label="Room"
          >
          </v-text-field>
          <v-text-field 
            solo-inverted flat hide-details dense rounded single-line
            v-model="name"
            label="Name"
          >
          </v-text-field>
        </div>
      </portal>

    </v-toolbar-items>
</template>

<script>
import { debounce } from 'lodash'

  export default {
    name: 'AppState',

    data() {
      return {
        room: this.$root.me.room,
        name: this.$root.me.name
      }
    },
    computed: {
      connectIcon() {
        return this.$root.me.listening ? 'mdi-lan-connect' : 'mdi-lan-disconnect';
      }
    },
  methods: {
    connect() {
      this.$root.me.setName(this.room, this.name)
    }
  }
}
</script>

<style scoped>
.outer {
  display: flex;
  flex-direction: row;
}
</style>
