<template>
  <v-container>
    <v-form>
      <v-text-field v-model="partner"></v-text-field>
      <v-btn @click="connect()">Connect</v-btn>
    </v-form>

    <videobox v-for="(conn, id) in conns" :key="id" :conn="conn">

    </videobox>
  </v-container>
</template>

<script>
import Videobox from '@/components/VideoBox.vue'

export default {
  name: 'Home',
  components: { Videobox },
  data() {
    return {
      partner: 'Bort2'
    }
  },
  computed: {
    conns() {
      return this.$root.me.connections;
    }
  },
  methods: {
    connect() {
      this.$root.me.connectTo(this.partner)
    }
  }
}
</script>

