<template>
    <v-card class="d-inline-block mx-auto">
        <v-container>
              <v-row justify="space-between">
                <v-col cols="auto">
                    <video ref="video" width="240" height="160"></video>
                </v-col>
              </v-row>
        </v-container>

        <v-card-subtitle class="pb-0">{{conn.name}}</v-card-subtitle>

        <v-card-actions>
            <v-btn icon @click="toggleCam()"><v-icon :color="color()">mdi-camera</v-icon></v-btn>
        </v-card-actions>
  </v-card>
</template>

<script>
export default {
    name: 'VideoBox',
    props: ['conn'],
    data() {
        return {}
    },
    computed: {
        camActive() { this.conn.active }
    },
    mounted() {
        this.conn.setVideo(this.$refs.video)
    },
    methods: {
        color() {
            return this.conn.stream ? 'primary' : ''
        },
        async toggleCam() {
            if (!this.conn.stream) {
                await this.conn.connect();
            }
            else {
                this.conn.disconnect();
            }
        }
    },
    watch: {
        'conn.stream': {
            handler() {
                this.conn.setVideo(this.$refs.video)
            }
        }
    }
}
</script>

<style scoped>
video.flipped {
    transform: rotateY(180deg)
}
</style>